@import url(https://fonts.googleapis.com/css?family=Montserrat);
html, body, #root {
  height: 100%;
}

html {
  /* Fallback background */
  /* background: #364b6d;  
  background: linear-gradient(115deg, #373b44, #4286f4); */
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
nav {
  display: flex;
  text-align: center;
  justify-content: space-between;
  border-bottom: 1px solid lightblue;
  align-items: center;
}

.nav-title, .nav-links {
  padding: 0 1rem;
  margin: 0;
}

.nav-title {
  font-size: 1.5em;
}

.nav-links {
  display: flex;
}

.nav-title:hover, .nav-links > li:hover {
  cursor: pointer;
  color: grey;
}

h3, li {
  padding: 0.5rem 0.25rem;
  margin: 0.5rem 0.25rem;
}
.dashboard-item {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  text-align: left;
  background: rgb(246, 249, 252);
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  box-shadow: 0.25rem 0.25rem 0.25rem darkslategrey;
  padding: 0.5rem;
  width: 80%;
  max-width: 200px;
  height: auto;
  font-size: 0.8rem;
  overflow: hidden;
}

.dashboard-item:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: 0.8s;
  transition: 0.8s;
  cursor: pointer;
}
.dashboard {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 1rem;
  padding: 2rem 0;
  justify-items: center;
}

.card {
  display: flex;
  flex-direction: column;
  /* grid-template-columns: 1fr; */
  justify-content: space-between;
  text-align: left;
  background: rgb(246, 249, 252);
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  box-shadow: 0.25rem 0.25rem 0.25rem darkslategrey;
  padding: 0.5rem;
  width: 80%;
  max-width: 400px;
  height: auto;
  /* flex-grow: 1; */
  font-size: 0.8rem;
  overflow: hidden;
}

.flexDiv {
  display: flex;
  flex-direction: column;
}

.card-expanded {
  /* position: fixed; */
  align-self: center;
  justify-self: center;
  text-align: left;
  background: rgb(246, 249, 252);
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  box-shadow: 0.25rem 0.25rem 0.25rem darkslategrey;
  padding: 0.5rem;
  width: 50%;
  max-width: 600px;
  height: auto;
  /* flex-grow: 1; */
  font-size: 0.8rem;
  overflow: hidden;
}

.card, .card-expanded:hover {
  cursor: pointer;
}

.cardBg {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #FFFFFF;
  justify-content: center;
}

.card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: 0.8s;
  transition: 0.8s;
}

h2, h3 {
  text-align: center;
}

h2, h3, li {
  margin: 0;
}

a {
  text-decoration: none;
}

li {
  font-size: 0.9rem;
}

h5 {
  font-weight: bold;
  font-size: 0.9rem;
  margin: 0;
  display: inline-block;
}

.rotten-tomato {
  width: 1rem;
  height: 1rem;
}

.readMore {
  display: inline-block;
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
    padding: 0.25rem;
    margin: 0;
    text-decoration: none;
    background: none;
    font-size: 0.9rem;
    cursor: pointer;
    text-align: center;
    -webkit-appearance: none;
    -moz-appearance: none;
}

.clickToExpand {
  /* display: inline-block; */
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  padding: 0.5rem 1rem;
  margin: 0;
  text-decoration: none;
  background: #0069ed;
  color: #ffffff;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  /* transition: background 250ms ease-in-out,  */
              /* transform 150ms ease; */
  -webkit-appearance: none;
  -moz-appearance: none;
}

.buttonContainer {
  display: flex;
  justify-content: center;
  align-self: center;
  padding-bottom: 0;
}
.cardList {
  display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 1rem;
    padding: 2rem 8rem;
    justify-items: center;
}

h2 {
  padding-top: 2rem;
}
.container {
  height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
  font-family: "Montserrat";
  text-align: center;
  /* background: linear-gradient(115deg, #373b44, #4286f4); */
}


/* Header */
.headerStyle {
  font-size: 1.5rem;
  font-weight: bold;
  padding: 2rem 0 0;
  justify-content: space-between;
}

h1 {
  margin: 0;
}

p {
  margin: 0.5rem 0;
}

/* Content */
.content {
  align-self: center;
}

.formStyle {
  width: 50%;
  min-width: 120px;
  max-width: 400px;
  margin: auto;
  padding: 1rem;
  border: 2px solid #233567;
  border-radius: 5%;
  box-shadow: 2px 2px 10px #000000;
  background: #f1f1f1;
  font-size: 1.2rem;
  font-family: "Calibri Light";
  text-align: left;
}

fieldset {
  border: 0;
}

.formTitleStyle {
  text-align: center;
}

ul {
  padding: 0.2rem 1rem;
  list-style: none;
}

.buttonContainer {
  text-align: center;
}

.buttonStyle {
  background: #ffffff;
  color: #000000;
  border-radius: 25px;
  border-color: #000000;
  padding: 1%;
  width: 35%;
  min-width: 70px;
  font-family: "Montserrat";
}

/* Footer */
.footerStyle {
  width: 100%;
}

/* Width Media Queries */
@media (max-width: 320px) {
  li {
    font-size: 0.8rem;
  }
  .headerStyle {
    font-size: 1rem;
    padding: 0.5rem;
  }
  .formStyle {
    padding: 0;
  }
}

